.form-container {
    margin: 20px auto;
    width: 100%;
    box-shadow: 0 5px 8px 0 rgba(112, 3, 30, 0.712), 0 7px 20px 0 rgba(0, 0, 0, 0.842);
    position: relative;
    height: 1750px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    color: rgb(12, 11, 12);
    background: linear-gradient(to right, #eaebe5, #1a060c);
    border-radius: 175px;
    -moz-border-radius: 175px;
    -webkit-border-radius: 175px;
}

.close-btn {
    position: absolute;
    top: 2%;
    right: 3%;
    font-size: 1.5rem;
    z-index: 1;
    color: rgb(61, 3, 27);
    cursor: pointer;
    border-radius: 175px;
    -moz-border-radius: 175px;
    -webkit-border-radius: 175px;
}

body {
