* {
    box-sizing: border-box;
}


/* Style the navbar */

.topnav {
    overflow: hidden;
    background-color: transparent;
}


/* Navbar links */

.topnav a {
    float: left;
    background-color: transparent;
    display: inline-block;
    color: rgba(24, 31, 128, 0.911);
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
}


/* Navbar links on mouse-over */

.topnav a:hover {
    background-color: transparent;
    color: rgb(16, 23, 128);
}


/* Active/current link */

.topnav a.active {
    background-color: transparent;
    color: rgb(202, 5, 176);
}


/* Style the input container */

.topnav .login-container {
    float: right;
}


/* Style the input field inside the navbar */

.topnav input[type=text] {
    padding: 6px;
    margin-top: 8px;
    font-size: 17px;
    border: none;
    width: 150px;
    background-color: rgba(255, 255, 255, 0.589);
    /* adjust as needed (as long as it doesn't break the topnav) */
}


/* Style the button inside the input container */

.topnav .login-container button {
    float: none;
    padding: 6px;
    margin-top: 8px;
    margin-left: 20px;
    background-color: transparent;
    font-size: 13px;
    border: none;
    cursor: pointer;
}

.topnav .login-container button:hover {
    background: linear-gradient(to right, #13130e, #5f021e);
}


/* Add responsiveness - On small screens, display the navbar vertically instead of horizontally */

@media screen and (max-width: 600px) {
    .topnav .login-container {
        float: none;
    }
    .topnav a,
    .topnav input[type=text],
    .topnav .login-container button {
        float: none;
        display: inline-flex;
        text-align: center;
        width: 20%;
        margin-left: 10px;
        padding: 1px;
    }
    .topnav input[type=text] {
        border: 1px solid rgb(114, 6, 87);
    }
}